@use 'base/variables' as v; // para importar las variables
@use 'base/mixins' as m;


.header {
    background-color: v.$verde;
    
    @include m.tablet{
        
        &.fijo { //clase de javascript
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .1rem .2rem .3rem #333;
        }
    
    }
    
    h1 {
        //este h1 esta anidado, es una de las funciones de sass
        color: v.$blanco;
        text-align: center;
    
        @include m.tablet {
        
            margin: 0;    
    }
    
        
    }

    .contenido-header {

        padding: 2rem;

        @include m.tablet {

            display: flex;
            justify-content: space-between;
            align-items: center;
        }

    }
}

.navegacion-principal {


    @include m.tablet {
        display: flex;
        gap: 2rem;
    }


    a {
        display: block;
        text-align: center;
        color: v.$blanco;
        font-size: 2.4rem;
        margin-bottom: 1rem;
        @include m.tablet {
            margin: 0;
            
        }

        &:hover {
            // el "&" hace que el pseudo elemento hover se compile junto al "a"
            color: v.$amarillo;

        }

    }

}