@use 'base/variables' as v;
@use 'base/mixins' as m;

.lineup {
    background-color: v.$rosa;
    padding: 5rem 0;

h3, p {
    text-align: center;
    color: v.$blanco;
}

.dia {

text-transform: uppercase;
margin-bottom: 5rem 0;

    }

}

.escenarios-contenedor {

    @include m.tablet {
        @include m.grid(2, 5rem);
        
    }

    p {
        color: v.$negro;
    }


    .escenario {

        padding-top: 2rem;

        .nombre-escenario {
            &:after {
                content: '';
                display: block;
                width: 20rem;
                height: 10rem;
                background-size: 15rem;
                background-repeat: no-repeat;
                background-position: center center;
                margin: 2rem auto 0 auto;
            }
        }

    }
}

.edm .nombre-escenario:after {

    background-image: url(../img/icono_edm.png);//los arhivos compilados quedan en la carpeta css ".." para salir de la carpeta y buscar la ubicación.

}

.rock .nombre-escenario:after {

    background-image: url(../img/icono_rock.png);//los arhivos compilados quedan en la carpeta css ".." para salir de la carpeta y buscar la ubicación.

}

.calendario {

    @include m.resetear-lista;
    li {
        padding: 2.5rem;
        font-weight: 700;
        
        span {
            font-weight: 400;
        }
    }
}


.bg-amarillo {

    background-color: v.$amarillo;

    li:nth-child(odd) { //se usa para seleccionar elementos de una lista "odd" va a seleccionar todos los que sean impares de los bloques de la lista.

    background-color:darken(v.$amarillo, 15%) ; //% para obscurecer


    }

}

.bg-verde {

    background-color: v.$verde;

    li:nth-child(odd) { //se usa para seleccionar elementos de una lista "odd" va a seleccionar todos los que sean impares de los bloques de la lista.

        background-color:darken(v.$verde, 10%) ; //% para obscurecer
    
    
        }

}